<template>
	<view class="container">
		<!-- 顶部选择区域 -->
		<scroll-view scroll-x class="scroll-view" :show-scrollbar="false">
			<view class="header">
				<view class="select-btn" :class="{ active: isAllSelected }" @tap="handleSelectAll">
					全部应用
				</view>
				<view class="divider"></view>
				<view class="ai-list">
					<view v-for="(item, index) in aiList" :key="index" class="select-btn"
						:class="{ active: selectedAIs.includes(item.id) }" @tap="handleSelectAI(item.id)">
						{{ item.name }}
					</view>
				</view>
			</view>
		</scroll-view>
		<!-- 中部内容区域 -->
		<view class="content">
			<view class="title-row">
				<view class="title">提示词模板</view>
				<uni-data-select v-model="selectedTemplate" :localdata="templateOptions" placeholder="预置模板"
					@change="handleTemplateChange" class="template-select"></uni-data-select>
			</view>
			<textarea v-model="promptTem.promptTemplate" class="template-input" placeholder="请输入提示词模板"
				:maxlength="-1" />
		</view>
		<!-- 底部保存按钮 -->
		<view class="footer">
			<button class="save-btn" :loading="isSaving" @tap="handleSave">
				保存配置
			</button>
		</view>
	</view>
</template>
<script>
	import {
		updateUserPromptTem,
		getUserPromptTem,
		getPromptTem
	} from '@/api/report'
	import storage from '@/utils/storage'
	import constant from '@/utils/constant'
	export default {
		data() {
			return {
				// AI列表数据
				aiList: [{
						id: 'Y1',
						name: 'Y1-腾讯元宝'
					},
					{
						id: 'Y2',
						name: 'Y2-deepseek'
					},
					{
						id: 'F1',
						name: 'F1-舆情助手'
					},
					{
						id: 'F2',
						name: 'F2-音乐助手'
					},
					{
						id: 'F3',
						name: 'F3-插画助手'
					},
					{
						id: 'F4',
						name: 'F4-写作助手'
					},
					{
						id: 'F5',
						name: 'F5-客服助手'
					}
				],
				userId: storage.get(constant.userId),
				corpId: storage.get(constant.corpId),
				username: storage.get(constant.openid),
				selectedTemplate: '',
				templateOptions: [{
						value: '1',
						text: '通用问答模板',
						content: '请针对以下问题给出详细的回答：{question}'
					},
					{
						value: '2',
						text: '代码生成模板',
						content: '请生成一段{language}代码实现以下功能：{feature}'
					},
					{
						value: '3',
						text: '文章润色模板',
						content: '请帮我润色以下文章，使其更加流畅自然：{article}'
					}
				],
				selectedAIs: [],
				isAllSelected: false,
				promptTem: {
					agentId: '',
					isAllSel: false,
					promptTemplate: '',
					userId: '',
				
				},
				isSaving: false
			}
		},
		computed: {
			isValid() {
				return (this.selectedAIs.length > 0 || this.isAllSelected) && this.promptTem.promptTemplate.trim() !== ''
			}
		},
		onShow() {
			this.getPromptTem()
		},
		methods: {
			getPromptTem(){
				getPromptTem(this.userId, 1).then(res => {
					this.templateOptions = res.data
				})
			},
			
			handleTemplateChange(value) {
				const template = this.templateOptions.find(item => item.value === value);
				if (template) {
					this.promptTem.promptTemplate = template.content;
				}
			},
			handleSelectAll() {
				if (this.isAllSelected) {
					this.promptTem.promptTemplate = ''
					this.isAllSelected = false
				} else {
					getUserPromptTem(this.userId, '').then(res => {
						this.promptTem.promptTemplate = res.data
					})
					this.isAllSelected = true
					this.selectedAIs = []
				}
			},
			handleSelectAI(id) {
				getUserPromptTem(this.userId, id).then(res => {
					if (res.data != null) {
						this.promptTem.promptTemplate = res.data
					} else {
						this.promptTem.promptTemplate = ''
					}

				})

				this.isAllSelected = false
				this.promptTem.agentId = id
				if (this.selectedAIs.includes(id)) {
					this.selectedAIs = []
				} else {
					this.selectedAIs = [id]
				}
			},
			handleSave() {
				// if (!this.isValid) return

				this.promptTem.isAllSel = this.isAllSelected

				this.promptTem.userId = this.userId
				

				this.isSaving = true
				updateUserPromptTem(this.promptTem).then(res => {
					if(res.code == 201){
						uni.showToast({
							title: res.messages,
							icon: 'none',
							duration: 1500,
						});
					}else{
						uni.showToast({
							title: '模板已保存',
							icon: 'success',
							duration: 1500,
						});
					}
					
					this.isSaving = false
				})

			}
		}
	}
</script>
<style>
	page {
		height: 100%;
	}

	.container {
		display: flex;
		flex-direction: column;
		height: 100%;
		background-color: #f5f5f5;
	}

	.scroll-view {
		background-color: #ffffff;
		padding: 24rpx 0;
		flex-shrink: 0;
	}

	.header {
		padding: 0 24rpx;
		white-space: nowrap;
	}

	.select-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 64rpx;
		padding: 0 32rpx;
		font-size: 14px;
		color: #666666;
		background-color: #f5f5f5;
		border-radius: 32rpx;
		transition: all 0.3s;
	}

	.select-btn.active {
		color: #ffffff;
		background-color: #1ec57c;
	}

	.divider {
		width: 100%;
		height: 2rpx;
		background-color: #e5e5e5;
		margin: 20rpx 0;
	}

	.ai-list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 20rpx;
	}

	.content {
		flex: 1;
		padding: 32rpx 24rpx;
		overflow: auto;
	}

	.title-row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 24rpx;
	}

	.title {
		font-size: 16px;
		color: #333333;
		font-weight: 500;
	}

	.template-select {
		width: 380rpx;
	}

	.template-input {
		width: 100%;
		height: 400rpx;
		background-color: #ffffff;
		border-radius: 16rpx;
		padding: 24rpx;
		font-size: 14px;
		line-height: 1.5;
		color: #333333;
	}

	.footer {
		padding: 24rpx;
		background-color: #ffffff;
		flex-shrink: 0;
	}

	.save-btn {
		width: 100%;
		height: 88rpx;
		line-height: 88rpx;
		background-color: #1ec57c;
		color: #ffffff;
		font-size: 16px;
		border-radius: 44rpx;
	}

	.save-btn[disabled] {
		background-color: #cccccc;
		color: #ffffff;
	}
</style>